<template>
  <div>
    <el-container>
      <el-header class="homeHeader">
        <div @click="isCollapse = isCollapse == false ? true : false">
          <i v-if="isCollapse == true" class="el-icon-s-fold" style="color: #ffffff">
          </i>
          <i v-if="isCollapse == false" class="el-icon-s-unfold" style="color: #ffffff">
          </i>
          <font id="title" style="margin-left: 20px"></font>
        </div>
        <div style="display: flex; align-items: center; margin-right: 7px">
          <span class="el-dropdown-link">
            <i class="el-icon-chat-dot-round" style="cursor: pointer;margin-right:10px" @click="pageChar" />
          </span>
          <el-dropdown class="userInfo" @command="commandHandler">
            <span class="el-dropdown-link">
              {{user.name}}
              <i><img :src="user.userface"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click="$router.push('/Person')"></el-dropdown-item>
              <el-dropdown-item @click="$router.push('/AlterPassword')"></el-dropdown-item>
              <el-dropdown-item command="logout" divided></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-container>
        <el-aside class="home-aside" :style="{ width:leftclass,height:leftclassh }">
          <el-scrollbar style="height: 100%; width: 100%;">
            <el-menu class="el-menu-vertical-demo"
                     style="height: 1020px;"
                     text-color="#bfcbd9"
                     :default-active="$route.path"
                     background-color="#324157"
                     router
                     unique-opened
                     active-text-color="#20a0ff"
                     :collapse="isCollapse"
                     @select="menusClick">
              <el-submenu :index="index+''"
                          v-for="(item, index) in routes"
                          :key="index">
                <template slot="title">
                  <i style="margin-right: 15px; " :class="item.iconcls"></i>
                  <span slot="title">{{ item.name}}</span>
                </template>
                <el-menu-item :index="child.path" v-for="( child, indexj ) in item.children" :key="indexj">
                  <!-- <i style="color: #409eff; margin:0 15px;" :class="child.iconcls"></i> -->
                  <span slot="title">{{ child.name}}</span>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-scrollbar>
        </el-aside>
        <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }" v-show=" this.$router.currentRoute.path!=='/home'">
              
            </el-breadcrumb-item>
            <!---->
            <el-breadcrumb-item>{{ this.$router.currentRoute.name}}</el-breadcrumb-item>
          </el-breadcrumb>
          <div :class="{homeRouterViewA: isa, homeRouterViewB: isb}" v-if="this.$router.currentRoute.path=='/home'">
            <Main />
          </div>
          <router-view :class="{homeRouterViewA: isa, homeRouterViewB: isb}" />
        </el-main>
      </el-container>
    </el-container>
    <el-dialog :visible.sync="dialogTableVisible" width="0%" custom-class="pagechar">
      <pageChar />
    </el-dialog>
  </div>
</template>

<script>
import Main from "./main";
import pageChar from "./chat/pageChat"
export default {

  name: "Home",
  data () {
    return {
      //sessionStorage
      user: JSON.parse(window.sessionStorage.getItem("user")),
      // 
      isCollapse: false,
      /**/
      loading: false,
      leftclass: '205px',
      leftclassh: '1020px',
      rightclass: '1325px',
      isa: true,
      isb: false,
      dialogTableVisible: false,
    }
  },
  mounted () {
  },
  methods: {
    activeMenu () {
      // path
      const path = this.$route.path
      alert(this.$route.path+"xsssssssssssss")
      // path/xxx(/xxx )
      if (path.startsWith('/emp')) {
        // 
        return '/emp'
      }
      return path
    },
    pageChar () {
      // this.$router.push("/chat");
      this.dialogTableVisible = true;
    },
    load () {
      this.loading = true
      setTimeout(() => {
        this.count += 3
        this.loading = false
      }, 2000)
    },
    /**/
    menusClick () {
      this.isCollapse = true;
    },
    /**/
    commandHandler (cmd) {
      if (cmd == 'logout') {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.getRequest('/logout');
          window.sessionStorage.removeItem("user");
          // 
          this.$store.commit('initRoutes', []);
          this.$router.replace("/");

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
  },
  computed: {
    routes () {
      return this.$store.state.routes
    },
    noMore () {
      return this.count >= 25
    },
    disabled () {
      return this.loading || this.noMore
    }

  },
  watch: {
    isCollapse: function (value) {
      if (value) {
        this.leftclass = '70px';
        this.leftclassh = '1020px';
        this.rightclass = "1400px";
        this.isa = false;
        this.isb = true;
      } else {
        this.leftclassh = '1020px';
        this.leftclass = '205px';
        this.rightclass = "1295px";
        this.isa = true;
        this.isb = false;
      }
    }
  },
  components: {
    Main, pageChar
  }
}
</script>

